import { ORDER_LIST } from '@/config/router.js';
import { loadIntegral } from '@/api/tabbar/todo.js';

export default {
	data() {
		return {
			navList: [
				{
					id: 1,
					icon: 'iconjifen',
					name: '我的积分',
					href: ''
				},
				{
					id: 2,
					icon: 'icon19',
					name: '兑换记录',
					href: `${ORDER_LIST}?title=兑换记录`
				}
			],
			integral: NaN
		};
	},
	created() {
		this.loadIntegral();
	},
	methods: {
		/**
		 * 加载积分
		 */
		loadIntegral: function() {
			loadIntegral().then(res => {
				this.integral = res.Remaining;
			});
		},
		
		linkToRoute: function(href) {
			uni.navigateTo({
				url: href
			});
		}
	}
};
